@import "../colors";

.maze {
  background: $lightPurple;
  width: fit-content;
  .cell {
    //width: 2vw;
    //height: 2vw;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1rem;
    box-sizing: border-box;

    //&.west {
    //  width: calc(2vw - 4px);
    //}
    //
    //&.north {
    //  height: calc(2vw - 4px);
    //}

    //@media only screen and (max-width: 1000px) {
      //width: calc(1em + 1vw);
      //height: calc(1em + 1vw);
      //&.west {
      //  width: calc(1em + 1vw - 4px);
      //}
      //
      //&.north {
      //  height: calc(1em + 1vw - 4px);
      //}
    //}

    .pony,
    .domokun,
    .door {
      width: 2vw;
      height: 2vw;
      background-repeat: no-repeat;
      background-size: contain;
    }

    .pony {
      background-image: url("../assets/pony.png");
    }

    .domokun {
      background-image: url("../assets/domokun.png");
    }

    .door {
      background-image: url("../assets/door.png");
    }
  }

  @keyframes moveLeft {
    from {
      background-position-x: 0;
      background-position-y: 144px;
    }
    to {
      background-position-x: 141px;
      background-position-y: 144px;
    }
  }

  @keyframes moveRight {
    from {
      background-position-x: 0;
      background-position-y: 96px;
    }
    to {
      background-position-x: 141px;
      background-position-y: 96px;
    }
  }

  @keyframes moveDown {
    from {
      background-position-x: 0;
    }
    to {
      background-position-x: 141px;
    }
  }

  @keyframes moveUp {
    from {
      background-position-x: 0;
      background-position-y: 48px;
    }
    to {
      background-position-x: 141px;
      background-position-y: 48px;
    }
  }

  .row {
    display: flex;
  }

  .west {
    border-left: solid 4px $mediumPurple;
  }

  .north {
    border-top: solid 4px $mediumPurple;
  }

  .east {
    border-right: solid 4px $mediumPurple;
  }

  .south {
    border-bottom: solid 4px $mediumPurple;
  }
}
