@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500;800&display=swap);
*{font-family:"Montserrat", sans-serif}body{margin:0}.app{width:100vw;height:100vh;display:flex}.app__sidebar{color:white;width:20%;background:#4a3656;padding:2rem}.app__maze-container{width:80%;background:#35023b}.app__title{color:#eeddf1;font-weight:900;grid-area:title}.app__parameters{display:flex;flex-direction:column;grid-area:parameters}.app__buttons{margin:20px 0;grid-area:buttons}.app__buttons--play{margin-right:10px;width:100%}.app__buttons--play,.app__buttons--retry{background:#766382;color:#fff;border:none;cursor:pointer;padding:10px}.app__mode{grid-area:mode}.app__mode>label{margin-right:8px}.app__keyboard-controls{display:flex;align-items:flex-end}.app__keyboard-controls-icon{margin:0 8px}.app__mouse-controls{position:absolute;right:30px;bottom:10px;display:grid;grid-template-areas:". up ." "left . right" ". down ."}.app__mouse-controls-up,.app__mouse-controls-down,.app__mouse-controls-left,.app__mouse-controls-right{width:2em;height:2em;margin:2px;cursor:pointer}.app__mouse-controls-up{grid-area:up}.app__mouse-controls-down{grid-area:down}.app__mouse-controls-left{grid-area:left}.app__mouse-controls-right{grid-area:right}.app__maze-container{display:inherit;align-items:center;justify-content:center}.app__instructions{color:#fff}.app__game-over,.app__game-won{display:flex;flex-direction:column;align-items:center}.app__game-over>p,.app__game-won>p{color:#fff;font-size:1em;text-transform:uppercase}.app__game-over .app__buttons--retry,.app__game-won .app__buttons--retry{width:120px}.app__gif{max-width:70%;height:auto;margin-bottom:40px}@media only screen and (max-width: 1280px){.app{flex-wrap:wrap-reverse}.app__sidebar,.app__maze-container{position:relative;width:100%}.app__sidebar{height:15%;display:grid;grid-template-areas:"parameters title" "parameters mode" "parameters buttons"}.app__title{margin-top:0;text-align:center}.app__mode{text-align:center}.app__buttons{width:60%;justify-self:center}.app__description,.app__keyboard-controls{display:none}.app__maze-container{height:85%}}@media only screen and (max-width: 600px){.app__title{display:none}.app__sidebar{padding:1rem;grid-template-areas:"parameters" "mode" "buttons";justify-content:center;height:auto}}

.slider{display:flex;align-items:center;margin:10px 0}.slider__input{border-radius:25px;height:10px;width:100%;outline:none;-webkit-appearance:none;margin:10px 8px;background:#35023b}.slider__input::-webkit-slider-thumb{-webkit-appearance:none;width:18px;height:18px;background:#eeddf1;border-radius:50%;cursor:pointer;box-shadow:0 0 1px 0 #000}.slider__input::-moz-range-thumb{-webkit-appearance:none;width:18px;height:18px;background:#eeddf1;border-radius:50%;cursor:pointer;box-shadow:0 0 1px 0 #000}@media only screen and (max-width: 1280px){.slider__input{width:50%}}@media only screen and (max-width: 600px){.slider{margin:0}}

.maze{background:#766382;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.maze .cell{display:flex;align-items:center;justify-content:center;font-size:1rem;box-sizing:border-box}.maze .cell .pony,.maze .cell .domokun,.maze .cell .door{width:2vw;height:2vw;background-repeat:no-repeat;background-size:contain}.maze .cell .pony{background-image:url(/Pony-challenge/static/media/pony.5c6ee56d.png)}.maze .cell .domokun{background-image:url(/Pony-challenge/static/media/domokun.49d63670.png)}.maze .cell .door{background-image:url(data:image/png;base64,)}@-webkit-keyframes moveLeft{from{background-position-x:0;background-position-y:144px}to{background-position-x:141px;background-position-y:144px}}@keyframes moveLeft{from{background-position-x:0;background-position-y:144px}to{background-position-x:141px;background-position-y:144px}}@-webkit-keyframes moveRight{from{background-position-x:0;background-position-y:96px}to{background-position-x:141px;background-position-y:96px}}@keyframes moveRight{from{background-position-x:0;background-position-y:96px}to{background-position-x:141px;background-position-y:96px}}@-webkit-keyframes moveDown{from{background-position-x:0}to{background-position-x:141px}}@keyframes moveDown{from{background-position-x:0}to{background-position-x:141px}}@-webkit-keyframes moveUp{from{background-position-x:0;background-position-y:48px}to{background-position-x:141px;background-position-y:48px}}@keyframes moveUp{from{background-position-x:0;background-position-y:48px}to{background-position-x:141px;background-position-y:48px}}.maze .row{display:flex}.maze .west{border-left:solid 4px #4a3656}.maze .north{border-top:solid 4px #4a3656}.maze .east{border-right:solid 4px #4a3656}.maze .south{border-bottom:solid 4px #4a3656}

